@import url("http://fonts.cdnfonts.com/css/pokemon-hollow");

body {
  // font-family: 'Press Start 2P', cursive;
  font-family: "Pokemon Hollow", sans-serif;
  font-size: 22px;
}

header {
  margin-top: 30px;
  font-size: 100px;
}

input {
  border: 0;
  outline: none;
  font-family: "Pokemon Hollow", sans-serif;
  font-size: 22px;
  text-align: center;
  padding-left: 5px;
  padding-right: 5px;
  opacity: 1;
  width: 180px;
  border-radius: 20px;
}

.app {
  display: grid;
  justify-items: center;
}

.appgrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2vw;
}

.searchbar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 20px;
  height: 50px;
  width: 200px;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  transition: height 0.2s;
  transition: background-color 0.2s;
  cursor: pointer;
}

::placeholder {
  color: black;
  opacity: 1;
}

.searchbar:hover {
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  transition: height 0.2s;
  background-color: rgba(255, 198, 100, 0.979);
  transition: background-color 0.2s;
}

// #searchbar-clicked {
//   background-color: rgba(255, 198, 100, 0.979);
//   transition: background-color 0.2s;
// }

.searchbar:active {
  background: #72aafd;
  top: 2px;
}

.filterheader {
  font-size: 20px;
  width: 100%;
}

#filter {
  display: flex;
  height: 150px;
  font-size: 13px;
  flex-direction: column;
}

#check {
  width: auto;
  font-size: 10px;
}

.checkdiv {
  margin-top: 7px;
  display: flex;
  flex-direction: row;
}

label {
  display: flex;
  flex-direction: column;
}

.gridchild {
  height: 250px;
  width: 200px;
  background-color: rgba(255, 198, 100, 0.979);
  border-radius: 2%;
  box-shadow: hsla(322, 87%, 56%, 0.4) 5px 5px,
    rgba(240, 46, 170, 0.3) 10px 10px, rgba(240, 46, 170, 0.2) 15px 15px,
    rgba(240, 46, 170, 0.1) 20px 20px, rgba(240, 46, 170, 0.05) 25px 25px;
}

.gridchild:hover {
  animation: shake 0.7s;
  background-color: rgb(255, 0, 157);
  animation-iteration-count: infinite;
  cursor: pointer;
}

.boxcontent {
  display: flex;
  width: 200px;
  height: 250px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
}

.pokemonname {
  padding-top: 5px;
  padding-bottom: 5px;
  width: 100%;
  background-color: rgb(255, 237, 197);
}

.like {
  width: 30px;
  height: auto;
  padding-bottom: 15px;
}

img {
  width: 70%;
  height: auto;
  padding-bottom: 20px;
}

ul {
  display: block;
}

li {
  list-style-type: none;
  text-align: center;
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}
